main {
    flex-grow: 1;
    padding: 10px;
    display: flex;
    background-color: #fafafa;
}

.app {
    display: flex;
    height: 100%;
    flex-direction: column;
}

html, body {
  height: 100%;
  padding: 0px;
  margin: 0px;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0px;
  margin: 0px;
  display: inline-block;
}
